import React from "react";
import "./Orders.css";
import { db } from "./firebase"; // this is for cloud fireStore
import { firebaseApp } from "./firebase"; // this is for realtime DataBase
import { useEffect } from "react";
import { useState } from "react";
import { StateContext } from "./StateProvider";
import { useContext } from "react";
import OrderList from "./OrderList";

const Orders = () => {
  const [state, dispatch] = useContext(StateContext);
  const [ordersArray, setOrderArray] = useState([]);
  // ordersArray = [{id:(paymentID), data:{amount:40, basketArray:[{},{},{},{}], created:(creation time)}]

  console.log("Orders rendered");

  useEffect(
    function () {
      console.log(state.currentUser);

      if (state.currentUser) {
        // this is for cloud fireStore
        {
          /*} db.collection("usersTable")
        .doc(state.currentUser?.uid)
        .collection("orders")
        .orderBy("created", "desc")
        .onSnapshot(function (snapshot) {
          console.log(snapshot.docs);
          setOrderArray(
            snapshot.docs.map((doc) => ({
              // doc : {id:(paymentID), data:{amount:40, basketArray:[{},{},{},{}], created:(creation time)}
              id: doc.id,
              data: doc.data(),
            }))
          );
        });
      */
        }

        // this is for realtime DataBase
        firebaseApp
          .database()
          .ref()
          .child("usersTable")
          .child(state.currentUser?.uid)
          .child("orders")
          .on("value", function (snapshot) {
            let valData = [];
            snapshot.forEach(function (childSnapshot) {
              console.log(childSnapshot);
              console.log(childSnapshot.key); //  "paymentID"
              console.log(childSnapshot.val()); // {amount:40, basketArray:[{},{},{},{}], created:(creation time)}
              valData.push({
                id: childSnapshot.key,
                data: childSnapshot.val(),
              });
              // valData = [{id:(paymentID), data:{amount:40, basketArray:[{},{},{},{}], created:(creation time)}]
              setOrderArray(
                valData.map((doc) => ({
                  // doc : {id:(paymentID), data:{amount:40, basketArray:[{},{},{},{}], created:(creation time)}
                  id: doc.id,
                  data: doc.data,
                }))
              );
            });
          });
      } else {
        setOrderArray([]);
      }
    },
    [state.currentUser]
  );
  return (
    <div className="orders">
      <h1>YOUR ORDERS</h1>
      <div className="orders_details">
        Number of Total Orders: {ordersArray.length}
        {console.log(ordersArray)}
        {/* ordersArray =[{id:(paymentID), data:{amount:40, basketArray:[{},{},{},{}], created:(creation time)}] */}
        {ordersArray?.map((order) => (
          <OrderList order={order} />
        ))}
        {/* order ={id:(paymentID), data:{amount:40, basketArray:[{},{},{},{}], created:(creation time)} */}
      </div>
    </div>
  );
};

export default Orders;
