import React from "react";
import { Link } from "react-router-dom";
import "./Register.css";
import { useState } from "react";
import { auth } from "./firebase";
import { useHistory } from "react-router";
import Alert from "@mui/material/Alert";

function Register() {
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errorAtRegister, setErrorAtRegister] = useState(false);

  function onChangeHandlerForEmail(event) {
    setEmail(event.target.value);
  }

  function onChangeHandlerForPassword(event) {
    setPassword(event.target.value);
  }

  function registerFunction(event) {
    event.preventDefault();
    auth
      .createUserWithEmailAndPassword(email, password)
      .then((auth) => {
        console.log(auth);
        if (auth) {
          history.push("/");
        }
      })
      .catch((error) => {
        setErrorAtRegister(true);
        console.log(error.message);
      });
  }
  return (
    <div className="register">
      <Link to="/">
        <img
          className="register_logo"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a9/Amazon_logo.svg/1024px-Amazon_logo.svg.png"
          alt=""
        />
      </Link>

      <div className="register_container">
        <h1 className="register_container_heading">Create Account</h1>
        <form>
          <h5 className="register_container_header">E-mail</h5>
          {errorAtRegister ? (
            <input
              onChange={onChangeHandlerForEmail}
              type="text"
              className="register_input_field"
              value={email}
              style={{
                borderColor: "red",
                boxShadow: "0 0 0 3px rgba(221,0,0,.1) inset",
              }}
            />
          ) : (
            <input
              onChange={onChangeHandlerForEmail}
              type="text"
              value={email}
              className="register_input_field"
            />
          )}
          {console.log("gggg")}

          <h5 className="register_container_header">Password</h5>
          {errorAtRegister ? (
            <input
              onChange={onChangeHandlerForPassword}
              type="password"
              value={password}
              className="register_input_field"
              style={{
                borderColor: "red",
                boxShadow: "0 0 0 3px rgba(221,0,0,.1) inset",
              }}
            />
          ) : (
            <input
              onChange={onChangeHandlerForPassword}
              type="password"
              value={password}
              className="register_input_field"
            />
          )}

          <p>
            {errorAtRegister ? (
              <Alert severity="error">Enter correct E-mail or Password</Alert>
            ) : (
              ""
            )}
          </p>
        </form>
        <Link to="/register" style={{ textDecoration: "none" }}>
          <button onClick={registerFunction} className="registrationButton">
            Create Your Account
          </button>
        </Link>
        <p className="register_container_para">
          By continuing, you agree to Amazon's Conditions of Use and Privacy
          Notice.
        </p>
        <p className="register_container_para">
          Already have an account? <Link to="/login">Sign-In</Link>
        </p>
      </div>
    </div>
  );
}

export default Register;
