import React from "react";
import "./Payment.css";
import { StateContext } from "./StateProvider";
import { useContext } from "react";
import { useState } from "react";
import { useHistory } from "react-router-dom";
//import axios from "./axios"; // this is for cloud fireStore
import axios from "axios";
import { Link } from "react-router-dom";
import CheckoutProduct from "./CheckoutProduct";
import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
import CurrencyFormat from "react-currency-format";
import { db } from "./firebase"; // this is for cloud fireStore
import { firebaseApp } from "./firebase"; // this is for realtime DataBase

const Payment = () => {
  console.log("Payement rendered");
  const [state, dispatch] = useContext(StateContext);

  const stripe = useStripe();
  const elements = useElements();
  const [succeeded, setSucceeded] = useState(false);

  const history = useHistory();
  const [error, setError] = useState(null);
  const [disabled, setDisabled] = useState(true);
  const [processing, setProcessing] = useState(false);

  //const [clientSecret, setClientSecret] = useState(true);

  let subtotal = 0;

  let isDisable = true;
  function calculateTotalPrice() {
    for (let i = 0; i < state.basketArray.length; i++) {
      subtotal = subtotal + state.basketArray[i].price;
    }
  }
  calculateTotalPrice();

  const submitHandlerFunction = async (event) => {
    //all the fancy stripe codes....

    event.preventDefault();
    setProcessing(true);

    const { errorValue, paymentMethod } = await stripe.createPaymentMethod({
      type: "card",
      card: elements.getElement(CardElement),
    });

    if (!errorValue) {
      //if there is no error(null)
      //Stripe expects the total in a currencies subunits
      //http:localhost:4040/payments/create??total=${subtotal * 100}
      try {
        const { id } = paymentMethod; //it is not needed here
        const response = await axios.post(
          `https://ecommerce-livedemo.herokuapp.com/payments/create?total=${
            subtotal * 100
          }`,
          {
            id: id, //it is not needed here
          }
        );

        if (response.data.success) {
          console.log("Successful Payment");

          setSucceeded(true);
          setError(null);
          setProcessing(false);

          //fireStore database part
          {
            /*db.collection("usersTable")
            .doc(state.currentUser?.uid) //  ? is not needed
            .collection("orders")
            .doc(id)
            .set({
              basketArray: state.basketArray,
              amount: subtotal,
              created: Date(),
            });
          */
          }

          // this is for realtime DataBase
          firebaseApp
            .database()
            .ref()
            .child("usersTable")
            .child(state.currentUser?.uid)
            .child("orders")
            .child(id)
            .set({
              basketArray: state.basketArray,
              amount: subtotal,
              created: Date(),
            });

          dispatch({
            type: "EMPTY_BASKET_ARRAY",
          });

          history.push("/orders");
        }
        if (response.data.success === false) {
          console.log("Unsuccessful Payment");
        }
      } catch (anyError) {
        console.log("Error", anyError);
      }
    } else {
      console.log(errorValue.message);
    }
  };

  function onChangeHandlerFunction(event) {
    //listen for changes in the CardElement
    //and display any errors as the customer types their card details

    setDisabled(event.empty); // event.empty = true
    console.log(disabled);
    setError(event.error ? event.error.message : "");
  }

  return (
    <div>
      {state.currentUser ? (
        <div className="payment">
          <h1>
            Checkout (
            <Link to="/checkout" className="payment_countItem">
              {state.basketArray.length} items
            </Link>
            )
          </h1>
          <div className="payment_section">
            <div className="payment_title">
              <h3>Delivery Address</h3>
            </div>
            <div className="payment_address">
              <p>{state.currentUser ? state.currentUser.email : ""}</p>
              <p>Dhaka</p>
              <p>Bangladesh</p>
            </div>
          </div>
          <div className="payment_section">
            <div className="payment_title">
              <h3>Review items and Delivery</h3>
            </div>
            <div className="payment_items">
              {state.basketArray.map((basketItem) => (
                <CheckoutProduct
                  id={basketItem.id}
                  title={basketItem.title}
                  price={basketItem.price}
                  image={basketItem.image}
                  rating={basketItem.rating}
                  hideButton={false}
                />
              ))}
            </div>
          </div>
          <div className="payment_section">
            <div className="payment_title">
              <h3>Payment Method</h3>
            </div>
            <div className="payment_details">
              {/*Stripe codes will be following*/}

              <form onSubmit={submitHandlerFunction} className="payment_form">
                <CardElement onChange={onChangeHandlerFunction} />
                <div className="payment_price_container">
                  <CurrencyFormat
                    renderText={(value) => <h3>Order Total {subtotal}</h3>}
                    decimalScale={2}
                    value={0}
                    displayType={"text"}
                    thousandSeparator={true}
                    prefix={"5"}
                  />
                  {(isDisable = processing || disabled || succeeded)}

                  <button
                    className={
                      isDisable ? "payment_button" : "payment_button_active"
                    }
                    type="submit"
                    disabled={isDisable}
                  >
                    <span>
                      {processing ? <p>Processing Now</p> : "Buy Now"}
                    </span>
                  </button>
                </div>
                {/*Errors*/}
                {error && <div>{error}</div>}
              </form>
            </div>
          </div>
        </div>
      ) : (
        history.push("/login")
      )}
    </div>
  );
};

export default Payment;
