import "./App.css";
import Header from "./Header";
import Home from "./Home";
import Checkout from "./Checkout";
import LogIn from "./LogIn";
import { useEffect } from "react";
import { StateContext } from "./StateProvider";
import { useContext } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { auth } from "./firebase";
import Payment from "./Payment";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import Orders from "./Orders";
import Register from "./Register";

const PUBLIC_KEY =
  "pk_test_51Jh096JEG44a6VQqrZKpR2WpNpqfYCK9HeO8lU6hXksmPmRapDZdVobynon5QTcNTq6hT4YJqjThejqhpfwILf1f00JlIW81pC";

const stripeTestPromise = loadStripe(PUBLIC_KEY);

function App() {
  const [state, dispatch] = useContext(StateContext);

  console.log("App rendered");

  useEffect(function () {
    //it will run only once when app component renders/loads....

    auth.onAuthStateChanged(function (authUser) {
      console.log("New Auth User from App.js :");

      console.log(authUser);

      if (authUser) {
        //the user jst logged in/ the user was logged in
        dispatch({
          type: "SET_CURRENT_USER",
          userCurrent: authUser,
        });
      } else {
        //the user is logged out
        dispatch({
          type: "SET_CURRENT_USER",
          userCurrent: null,
        });
      }
    });
  }, []);
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path="/register">
            <Register />
          </Route>
          <Route path="/login">
            <LogIn />
          </Route>
          <Route path="/checkout">
            <Header />
            <Checkout />
          </Route>
          <Route path="/payment">
            <Header />
            <Elements stripe={stripeTestPromise}>
              <Payment />
            </Elements>
          </Route>
          <Route path="/orders">
            <Header />
            <Orders />
          </Route>
          <Route path="/">
            <Header />
            <Home />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
