import React from "react";
import { Link } from "react-router-dom";
import "./LogIn.css";
import { useState } from "react";
import { auth } from "./firebase";
import { useHistory } from "react-router";
import Alert from "@mui/material/Alert";

const LogIn = () => {
  console.log("LogIn rendered");
  const history = useHistory();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errorAtSignIn, setErrorAtSignIn] = useState(false);

  function onChangeHandlerForEmail(event) {
    setEmail(event.target.value);
  }

  function onChangeHandlerForPassword(event) {
    setPassword(event.target.value);
  }

  function signInFunction(event) {
    event.preventDefault();
    auth
      .signInWithEmailAndPassword(email, password)
      .then((auth) => {
        history.goBack();
        console.log("New Auth User Created After Sign In:");
        console.log(auth);
        if (auth) {
        }
      })
      .catch((error) => {
        setErrorAtSignIn(true);
        console.log("Error-message " + error.message);
      });
    setEmail("");
    setPassword("");
  }

  return (
    <div className="login">
      <Link to="/">
        <img
          className="login_logo"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/a/a9/Amazon_logo.svg/1024px-Amazon_logo.svg.png"
          alt=""
        />
      </Link>
      <div className="login_container">
        <h1 className="login_container_heading">Sign In</h1>
        <form>
          <h5 className="login_container_header">E-mail</h5>
          {errorAtSignIn ? (
            <input
              onChange={onChangeHandlerForEmail}
              type="text"
              className="login_input_field"
              value={email}
              style={{
                borderColor: "red",
                boxShadow: "0 0 0 3px rgba(221,0,0,.1) inset",
              }}
            />
          ) : (
            <input
              onChange={onChangeHandlerForEmail}
              type="text"
              value={email}
              className="login_input_field"
            />
          )}

          <h5 className="login_container_header">Password</h5>
          {errorAtSignIn ? (
            <input
              onChange={onChangeHandlerForPassword}
              type="password"
              value={password}
              className="login_input_field"
              style={{
                borderColor: "red",
                boxShadow: "0 0 0 3px rgba(221,0,0,.1) inset",
              }}
            />
          ) : (
            <input
              onChange={onChangeHandlerForPassword}
              type="password"
              value={password}
              className="login_input_field"
            />
          )}

          <p>
            {errorAtSignIn ? (
              <Alert severity="error">Enter correct E-mail or Password</Alert>
            ) : (
              ""
            )}
          </p>
          <button onClick={signInFunction} className="signInButton">
            Sign In
          </button>
        </form>

        <p className="register_container_para">
          New to Amazon?{" "}
          <Link to="/register" style={{ textDecoration: "none" }}>
            Sign-Up
          </Link>
        </p>
      </div>
    </div>
  );
};

export default LogIn;
