const reducerFunction = (state, action) => {
  // is is a general javascript function not a component.

  console.log(action);

  switch (action.type) {
    case "ADD_TO_BASKET":
      return { ...state, basketArray: [...state.basketArray, action.item] };

    case "REMOVE_FROM_BASKET":
      const index = state.basketArray.findIndex(
        (basketItem) => basketItem.id === action.id
      );
      let newBasket = [...state.basketArray];
      if (index >= 0) {
        newBasket.splice(index, 1);
      } else {
        console.log(
          `Can not remove product (id: ${action.id}) as it is not basket`
        );
      }
      return { ...state, basketArray: newBasket };

    case "SET_CURRENT_USER":
      return { ...state, currentUser: action.userCurrent };

    default:
      return state;
  }
};

export default reducerFunction;
