import React from "react";
import "./Product.css";
import { StateContext } from "./StateProvider";
import { useContext } from "react";
import Rating from "@mui/material/Rating";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";

function Product({ id, title, price, image, rating }) {
  const [state, dispatch] = useContext(StateContext);

  console.log("Product rendered currentState", state);

  function addToBasket() {
    //dispatch the item into the data layer

    dispatch({
      type: "ADD_TO_BASKET",
      item: {
        id: id,
        title: title,
        price: price,
        image: image,
        rating: rating,
      },
    });
  }

  return (
    <Card
      sx={{ flexDirection: "column", width: "100%", p: "20px", m: "10px" }}
      style={{ borderColor: "green", borderWidth: "4px" }}
    >
      <Typography gutterBottom variant="inherit" component="div">
        {title}
      </Typography>
      <CardMedia
        component="img"
        height="auto"
        width="100%"
        image={image}
        alt="green iguana"
      />
      <CardContent>
        <Typography gutterBottom variant="inherit" component="div">
          ${price}
        </Typography>
        <Rating name="half-rating" defaultValue={rating} precision={0.5} />
      </CardContent>
      <CardActions>
        <Button size="small" onClick={addToBasket} className="product_button">
          Add to basket
        </Button>
      </CardActions>
    </Card>
  );
}

export default Product;

